import React from "react";
import Hover from "./Hover";
import Svg from "./Svg";

const icons = [
  {
    name: "linkedin",
    link: "",
  },
  {
    name: "instagram",
    link: "",
  },
  {
    name: "behance",
    link: "",
  },
  {
    name: "dribble",
    link: "",
  },
  {
    name: "telegram",
    link: "",
  },
];

const SocialIcons = () => {
  return (
    <div className="social-icons">
      {icons.map((icon, index) => (
        <Hover key={index}>
          <a href={icon.link}>
            <Svg name={icon.name} />
          </a>
        </Hover>
      ))}
    </div>
  );
};

export default SocialIcons;
