import React, { useRef } from "react";
import Svg from "../../components/shareable/Svg";

import Video from "../../assets/video/reel.mp4";
import MovableButton from "../../components/shareable/MovableButton";
import Parallax from "../../components/parallax/parallax";

const ShowReel = () => {
  const showReelRef = useRef(null);
  const videoRef = useRef(null);

  return (
    <Parallax containerRef={showReelRef} elementRef={videoRef}>
      <section id="show-reel" className="show-reel container" ref={showReelRef}>
        <div className="wrapper  animate-element fade-up onload">
          <video
            id="showreel-video"
            ref={videoRef}
            autoPlay
            muted
            playsInline
            loop
          >
            <source src={Video} type="video/mp4" />
            Your browser does not support the video tag.
          </video>
        </div>
        <MovableButton type="btn" name="play" containerRefs={[showReelRef]} />
      </section>
    </Parallax>
  );
};

export default ShowReel;
