import React, { useEffect, useState } from "react";

import Home from "./pages/Home/Home";
import Projects from "./pages/Projects/Projects";
import { animateElement, handleLoad } from "./scripts/textAnimation";
import Navigation from "./components/navigation/Navigation";
import Footer from "./components/footer/Footer";
import About from "./pages/About/About";
import Services from "./pages/Services/Services";
import Contact from "./pages/Contact/Contact";

import { Routes, Route, useLocation } from "react-router-dom";
import Credd from "./pages/ProjectPost/Credd";

const App = () => {
  const [projects, setProjects] = useState([]);

  const [arr, setArr] = useState([]);

  const location = useLocation();

  useEffect(() => {
    const initLoad = () => {
      fetch(process.env.PUBLIC_URL + "/json/projects.json")
        .then((res) => res.json())
        .then((data) => {
          setProjects([...data]);

          const arr = [];

          for (let i = 0; i < data.length; i++) {
            const component =
              data[i].link.slice(0, 1).toUpperCase() + data[i].link.slice(1);

            const Item = React.lazy(() =>
              import(`./pages/ProjectPost/${component}`)
            );

            arr[i] = <Item />;
          }

          setArr(arr);
        });
    };

    initLoad();
  }, []);

  useEffect(() => {
    const initLoad = () => {
      if (document.readyState === "complete") {
        handleLoad();
      } else {
        setTimeout(() => {
          initLoad();
        }, 50);
      }
    };

    initLoad();

    window.addEventListener("resize", handleLoad);

    window.scrollTo({
      top: 0,
      left: 0,
      behavior: "instant",
    });

    return () => {
      window.removeEventListener("resize", handleLoad);
    };
  }, [location]);

  useEffect(() => {
    window.addEventListener("scroll", animateElement);
    return () => window.removeEventListener("scroll", animateElement);
  }, []);

  useEffect(() => {
    window.history.scrollRestoration = "manual";
  }, []);

  console.log(projects, "home");

  return (
    <>
      <Navigation />
      <Routes>
        <Route path="/" element={<Home projects={projects} />} />
        <Route path="/projects" element={<Projects projects={projects} />} />
        <Route path="/about" element={<About />} />
        <Route path="/services" element={<Services projects={projects} />} />
        <Route path="/contact" element={<Contact />} />

        {/* {arr.map((Item, index) => {
          return (
            <Route
            key={index}
            path={`/${projects[index].link}`}
            element={Item}
            />
            );
          })} */}
        <Route path="/credd" element={<Credd />} />
        <Route path="/marmelo" element={<Credd />} />
      </Routes>

      <Footer />
    </>
  );
};

export default App;
