import React, { useRef } from "react";
import Parallax from "../../components/parallax/parallax";

import Image from "../../assets/images/about2.jpg";

const FullImage = () => {
  const containerRef = useRef(null);
  const elementRef = useRef(null);

  return (
    <Parallax containerRef={containerRef} elementRef={elementRef}>
      <section className="full-image section-space" ref={containerRef}>
        <img src={Image} alt="" ref={elementRef} />

        <div className="content container-narrow animate-element fade-up">
          <div className="subtitle">Never compromise on quality</div>
          <h2>
            At the heart of everything we do is this idea of making business
            better.
          </h2>
        </div>
      </section>
    </Parallax>
  );
};

export default FullImage;
