import React, { useRef, useState, useEffect } from "react";
import Parallax from "../../components/parallax/parallax";

const TemplateHero = ({ imageFolder, imageName, title }) => {
  const [height, setHeight] = useState(window.innerHeight);

  useEffect(() => {
    const handleResize = () => {
      const nav = document.querySelector("nav");

      setHeight(
        window.innerHeight -
          (nav.getBoundingClientRect().height + nav.getBoundingClientRect().top)
      );
    };

    handleResize();

    window.addEventListener("resize", handleResize);

    return () => window.removeEventListener("resize", handleResize);
  });

  const containerRef = useRef(null);
  const elementRef = useRef(null);

  return (
    <>
      <section className="template-hero" style={{ height: height }}>
        <div className="container-narrow">
          <div className="subtitle animate-element fade-up onload">
            SERVICE - UI/UX
          </div>
          <h1
            className="separate-text animate-element fade-up-line onload"
            data-text={title}
          >
            {title}
          </h1>
        </div>
      </section>
      <Parallax containerRef={containerRef} elementRef={elementRef}>
        <div className="img-wrapper" ref={containerRef}>
          <img
            ref={elementRef}
            src={
              process.env.PUBLIC_URL +
              `/projectImage/${imageFolder}/${imageName}.jpg`
            }
            alt=""
          />
        </div>
      </Parallax>
    </>
  );
};

export default TemplateHero;
