import React, { useRef } from "react";

import LocationImage from "../../assets/images/location.png";
import Parallax from "../../components/parallax/parallax";
import Svg from "../../components/shareable/Svg";

const Location = () => {
  const containerRef = useRef(null);
  const elementRef = useRef(null);

  return (
    <Parallax containerRef={containerRef} elementRef={elementRef}>
      <section
        className="location section-space container-narrow"
        ref={containerRef}
      >
        <img src={LocationImage} alt="" ref={elementRef} />

        <div className="info-details">
          <div className="icon-circle">
            <Svg name="location" />
          </div>
          <div className="info-text">
            <h2>Serbia, Belgrade</h2>
            <p>Hadzi-Ruvimova 15, Vracar</p>
          </div>
        </div>
      </section>
    </Parallax>
  );
};

export default Location;
