import React from "react";
import Svg from "../../components/shareable/Svg";
import TeamItem from "./TeamItem";

const membersGroup1 = [
  {
    name: "Vasilije",
    position: "CEO",
    country: "Serbia",
  },
  {
    name: "Alexey",
    position: "Developer",
    country: "Ukraine",
  },
];

const membersGroup2 = [
  {
    name: "Lazar",
    position: "Developer",
    country: "Serbia",
  },
  {
    name: "Nikolay",
    position: "Designer",
    country: "Russia",
  },
  {
    name: "Ivan",
    position: "Designer",
    country: "Ukraine",
  },
];

const Team = () => {
  return (
    <section className="team container-narrow section-space animate-element fade-up">
      <h2>Our team consists of</h2>

      <div className="members">
        {membersGroup1.map((member, index) => (
          <TeamItem key={index} item={member} />
        ))}
      </div>

      <div className="members">
        {membersGroup2.map((member, index) => (
          <TeamItem key={index} item={member} />
        ))}
      </div>

      <div className="located">
        <div className="icon">
          <Svg name="location" />
        </div>
        <p>
          <span className="opacity">Based in</span> <br />
          Serbia, Belgrade
        </p>
      </div>
    </section>
  );
};

export default Team;
