import React, { useState, useEffect, useRef } from "react";

import "./footer.css";
import SocialIcons from "./../shareable/SocialIcons";
import Button from "./../shareable/Button";
import Hover from "./../shareable/Hover";

const Footer = () => {
  const [translateValue, setTranslateValue] = useState(100);
  const containerRef = useRef(null);

  useEffect(() => {
    const handleTranslate = () => {
      if (
        containerRef.current.getBoundingClientRect().top - window.innerHeight <
        0
      ) {
        let disableMove = false;

        if (disableMove) return;
        disableMove = true;
        setTimeout(() => {
          disableMove = false;
        }, 20);

        setTranslateValue(
          ((containerRef.current.getBoundingClientRect().bottom -
            window.innerHeight) /
            containerRef.current.getBoundingClientRect().height) *
            100
        );
      }
    };

    document.addEventListener("scroll", handleTranslate);

    return () => document.removeEventListener("scroll", handleTranslate);
  }, []);

  return (
    <footer className="white-section" ref={containerRef}>
      <div
        className="container-narrow"
        style={{
          transform: `translateY(-${translateValue}px)`,
        }}
      >
        <div className="top">
          <h2>
            Have an idea? <br />
            Let’s build it!
          </h2>
          <div className="button-wrapper">
            <Button type="dark" text="Get a quote" />
          </div>
        </div>
        <div className="bottom">
          <div className="line">
            <div className="contact-info">
              <a href="mailto:hi@handmade.company">
                <Hover>hi@handmade.company</Hover>
              </a>
              <a href="tel:+381 60 144 3221">
                <Hover>+381 60 144 3221</Hover>
              </a>
            </div>
            <div className="back">
              <Hover>Back to top</Hover>
            </div>
          </div>
          <div className="line">
            <div className="copyrights">© All Rights Reserved</div>
            <SocialIcons />
          </div>
        </div>
      </div>
    </footer>
  );
};

export default Footer;
