import React, { useEffect } from "react";

const Parallax = ({ containerRef, elementRef, children }) => {
  useEffect(() => {
    const parallaxEffect = () => {
      let breakPoint = +(
        containerRef.current.getBoundingClientRect().height -
        elementRef.current.getBoundingClientRect().height
      ).toFixed();

      const percentage =
        ((containerRef.current.getBoundingClientRect().top -
          window.innerHeight) /
          (window.innerHeight +
            containerRef.current.getBoundingClientRect().height)) *
        -1;

      if (containerRef.current.getBoundingClientRect().bottom < 0) return;
      elementRef.current.style.transform = `translateY(${
        breakPoint * percentage
      }px)`;
    };

    if (window.innerWidth > 700) {
      parallaxEffect();
      document.addEventListener("scroll", parallaxEffect);
    }

    const removeEvent = () => {
      if (window.innerWidth > 700) {
        document.addEventListener("scroll", parallaxEffect);
      } else {
        document.removeEventListener("scroll", parallaxEffect);
      }
    };

    window.addEventListener("resize", removeEvent);

    return () => {
      document.removeEventListener("scroll", parallaxEffect);
      window.removeEventListener("resize", removeEvent);
    };
  }, [containerRef, elementRef]);

  return <>{children}</>;
};

export default Parallax;
