import React, { useState } from "react";
import Svg from "./../../components/shareable/Svg";

const ServicesItem = ({ projects, item }) => {
  const [sliderIndex, setSliderIndex] = useState(0);
  const [curtainActive, setCurtainActive] = useState(false);
  const [disable, setDisable] = useState(false);

  const changeSlider = (dir) => {
    setCurtainActive(true);

    setTimeout(() => {
      setTimeout(() => {
        setCurtainActive(false);

        setTimeout(() => {
          setDisable(false);
        }, 450);
      }, 50);
      if (dir === "next") {
        setSliderIndex((prev) => {
          let index = prev + 1;

          if (index > projects.length - 1) {
            index = 0;
          }

          return index;
        });
      }

      if (dir === "prev") {
        setSliderIndex((prev) => {
          let index = prev - 1;

          if (index < 0) {
            index = projects.length - 1;
          }

          return index;
        });
      }
    }, 500);
  };

  return (
    <div className="services-item section-space animate-element fade-up">
      <div className="slider ">
        <div className="images">
          <div
            className={`curtain ${curtainActive ? "curtain-active" : ""}`}
          ></div>

          {projects.map((project, index) => (
            <img
              src={
                process.env.PUBLIC_URL + `/projectImage/${project?.image}.jpg`
              }
              key={index}
              alt=""
              style={{ zIndex: sliderIndex === index ? 2 : 1 }}
            />
          ))}
        </div>
        <div className="controls">
          <div className="case">
            <span className="opacity">Recent Case </span>
            {projects[sliderIndex]?.name}
          </div>
          <div className="arrows">
            <div
              className="left arrow"
              onClick={() => {
                if (!disable) {
                  changeSlider("prev");
                  setDisable(true);
                }
              }}
            >
              <Svg name="sliderArrow" />
            </div>
            <div
              className="right arrow"
              onClick={() => {
                if (!disable) {
                  changeSlider("next");
                  setDisable(true);
                }
              }}
            >
              <Svg name="sliderArrow" />
            </div>
          </div>
        </div>
      </div>
      <div className="info">
        <div>
          <h2 className="">{item?.title}</h2>
          <p className="description">{item?.desc}</p>
        </div>

        <div className="technology">
          <div className="title">
            <p className="">{`IN-HOUSE CAPABILITIES`}</p>
          </div>
          <div className="list">
            <p className="">{item?.capabilities}</p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ServicesItem;
